const StickyContent = ({ title }) => (
  <div
    sx={{
      display: "flex",
      justifyContent: "space-between",
      color: "white",
      alignItems: "center",
      py: [rem(15), rem(15), rem(17.4)],
    }}
  >
    <button
      onClick={() => {
        window.history.go(-1);
      }}
      sx={{
        transform: "translateY(-5px)",
        color: "white",
        fontSize: [rem(14), null, rem(18)],
        fontWeight: 600,
        svg: {
          transform: [
            "rotate(180deg) translateY(-7px)",
            null,

            "rotate(180deg) translateY(-5px)",
          ],
          marginRight: rem(3),
        },
      }}
    >
      <ArrowRight />
      Back
    </button>

    <div>
      <h1
        sx={{
          fontSize: rem(24),
          my: 0,
          display: ["none", "block", null],
          letterSpacing: "-0.5px",
        }}
      >
        {title}
      </h1>
    </div>

    <button
      onClick={() => {
        window.scrollTo({ top: 0, behavior: "smooth" });
      }}
      sx={{
        transform: "translateY(-5px)",
        color: "white",
        fontSize: [rem(14), null, rem(18)],
        fontWeight: 600,
        svg: {
          transform: "rotate(270deg) translateY(2px) translateX(-6px)",
          marginRight: rem(3),
        },
      }}
    >
      Top
      <ArrowRight />
    </button>
  </div>
);

export const DefaultHeader = () => {
  return (
    <div
      sx={{
        bg: "csiDarkGrey",
      }}
    >
      <Container
        customStyles={{
          width: ["95%", null, "90%"],
        }}
      >
        <HeaderContent />
      </Container>
    </div>
  );
};
const StickyHeader = ({ title }: ComponentProps) => {
  const [sticky, setSticky] = useState(false);

  useScrollPosition(
    ({ currPos }) => {
      if (currPos.y >= 80) {
        setSticky(true);
      } else {
        setSticky(false);
      }
    },
    [],
    null,
    true,
    80
  );

  return (
    <div>
      <div
        sx={{
          position: "static",
          bg: "csiDarkGrey",
          width: "100%",
        }}
      >
        <Container
          customStyles={{
            width: ["95%", null, "90%"],
          }}
        >
          <HeaderContent />
        </Container>
      </div>

      <div
        sx={{
          visibility: sticky ? "visible" : "hidden",
          opacity: sticky ? 1 : 0,
          position: "fixed",
          bg: "csiDarkGrey",
          width: "100%",
          zIndex: 1000,
          top: 0,
          transition: "visibility 0.3s linear, opacity 0.3s linear",
        }}
      >
        <Container
          customStyles={{
            width: ["95%", null, "90%"],
          }}
        >
          <StickyContent title={title} />
        </Container>
      </div>
    </div>
  );
};

interface ComponentProps {
  title?: string;
  stickyMode?: Boolean;
}

const Main = ({ title, stickyMode }: ComponentProps) => {
  if (stickyMode) {
    return <StickyHeader title={title} />;
  } else {
    return <DefaultHeader />;
  }
};
export default Main;
