import React from "react";
import Helmet from "react-helmet";
import { useStaticQuery, graphql } from "gatsby";

function SEO({ location, lang, defaultTitle, type, metadata }) {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            hostname
          }
        }
      }
    `
  );

  let pathname = location.pathname || "/";
  if (pathname.endsWith("/"))
    pathname = pathname.substring(0, pathname.length - 1);
  const canonicalUrl = `${site.siteMetadata.hostname}${pathname}/`;

  const description = metadata
    ? metadata.metaDescription
    : site.siteMetadata.description;

  const socialImageUrl = metadata ? metadata.socialImage : "";
  const title = metadata
    ? `${metadata.metaTitle || defaultTitle} | SEES`
    : `${site.siteMetadata.title}`;

  const meta = [
    {
      name: "description",
      content: description,
    },
    {
      property: "og:title",
      content: title,
    },
    {
      property: "og:description",
      content: description,
    },
    {
      property: "og:type",
      content: type || "website",
    },
    {
      property: "og:url",
      content: canonicalUrl,
    },
    {
      property: "og:image",
      content: socialImageUrl,
    },
    {
      name: "twitter:card",
      content: "summary",
    },
    {
      name: "twitter:title",
      content: title,
    },
    {
      name: "twitter:description",
      content: description,
    },
    {
      property: "twitter:image",
      content: socialImageUrl,
    },
  ];

  return (
    <Helmet
      htmlAttributes={{ lang }}
      title={title}
      meta={meta}
      link={[
        {
          rel: "canonical",
          key: canonicalUrl,
          href: canonicalUrl,
        },
        {
          rel: "icon",
          type: "image/png",
          sizes: "16x16",
          href: "/icons/favicon-16x16.png",
        },
        {
          rel: "manifest",
          href: "/icons/manifest.json",
        },
      ]}
    >
    </Helmet>
  );
}

SEO.defaultProps = {
  lang: "en-AU",
};

export default SEO;
